import React from "react";
import Joi from "joi";
import Form from "./common/Form";
import auth from "../services/authService";
import { Redirect } from "react-router-dom";

// rver frn Form.jsx.
// Nytt formulr behver state med data och errors, Joi-schema och doSubmit.
// Kan sedan anvnda renderInput och renderButton frn Form.
// rver ven frn Component genom frldrarkomponenten Form
class LoginForm extends Form {
  state = {
    data: { username: "", password: "" },
    errors: {
      username: "",
      password: "",
    },
  };

  // Installerat biblioteket Joi. Stter reglerna fr mina inputflt.
  schema = Joi.object({
    username: Joi.string().required().min(2).label("Username"),
    password: Joi.string().required().min(4).label("Password"),
  });

  // Vad ska hnda nr man tycker p knappen?
  doSubmit = async () => {
    try {
      await auth.login(this.state.data);
      const { state } = this.props.location;
      window.location = state ? state.from : "/intensive-foods/";
    } catch (error) {
      if (error.response.status === 400) {
        const errors = { username: error.response.data };
        this.setState({ errors });
      }
    }
  };

  render() {
    if (auth.getCurrentUser()) return <Redirect to="/intensive-foods/" />;

    return (
      <form className="m-3" onSubmit={this.handleSubmit}>
        <h1>Log in</h1>
        {this.renderInput("username", "Username")}
        {this.renderInput("password", "Password", "", "password")}
        {this.renderButton("Log in")}
      </form>
    );
  }
}

export default LoginForm;
